.container.contact_container{
    width: 50%;
    gap:12%;

}
div.form
{
    display: block;
    text-align: center;
}
form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none ;
    color: var(--color-white)
}